import React, { useReducer } from "react";
import axios from "axios";
import humps from "humps";
import qs from "qs";
import Modal from "react-modal";

const initialState = {
    images: [],
    nextToken: "",
    currentPage: 0,
    totalCount: 0,
    isTruncated: false,
    isBucketFetching: false,
};

const ImageBucketContext = React.createContext(initialState);

const BUCKET_FETCH_SUCCESS = "BUCKET_FETCH_SUCCESS";
const BUCKET_FETCH_STARTED = "BUCKET_FETCH_STARTED";

const reducer = (state, action) => {
    switch (action.type) {
        case BUCKET_FETCH_SUCCESS:
            return {
                ...state,
                images: [...state.images, ...action.images],
                nextToken: action.nextToken,
                isTruncated: action.isTruncated,
                isBucketFetching: false,
            };
        case BUCKET_FETCH_STARTED:
            return {
                ...state,
                isBucketFetching: true,
            };
        default:
            return state;
    }
};

const ImageBucketProvider = ({ children }) => {
    const [bucketState, dispatch] = useReducer(reducer, initialState);

    const actions = {
        bucketFetching: () => {
            dispatch({
                type: BUCKET_FETCH_STARTED,
            });
        },
        bucketsFetched: (images, nextToken, isTruncated) => {
            console.log(nextToken, isTruncated);
            dispatch({
                type: BUCKET_FETCH_SUCCESS,
                images,
                nextToken,
                isTruncated,
            });
        },
    };

    const fetchBucket = (nextToken = "") => {
        const query = qs.stringify(
            { nextToken, count: 200 },
            { arrayFormat: "brackets" }
        );
        const url = `http://localhost:3005/list-files?${query}`;
        actions.bucketFetching();
        return axios
            .get(url)
            .then((data) => data.data)
            .then((data) => {
                const {
                    Contents = [],
                    KeyCount = 0,
                    IsTruncated,
                    NextContinuationToken,
                } = data ?? {};
                actions.bucketsFetched(
                    Contents,
                    NextContinuationToken,
                    IsTruncated
                );
            });
    };

    return (
        <ImageBucketContext.Provider
            value={{
                images: bucketState.images,
                nextToken: bucketState.nextToken,
                isBucketFetching: bucketState.isBucketFetching,
                fetchBucket,
            }}
        >
            {children}
        </ImageBucketContext.Provider>
    );
};

export { ImageBucketContext, ImageBucketProvider };
