import React, { useCallback, useEffect, useState } from "react";
import {
    CellMeasurer,
    createMasonryCellPositioner,
    CellMeasurerCache,
    Masonry,
    InfiniteLoader,
} from "react-virtualized";
import ImageRenderer from "./ImageRenderer";
import ImageModal from "./ImageModal";

const columnWidth = 325;
const defaultHeight = 300;
const defaultWidth = columnWidth;
const baseURL = `https://vvpa-model-gallery.s3.ap-south-1.amazonaws.com/`;

const cache = new CellMeasurerCache({
    defaultHeight,
    defaultWidth,
    fixedWidth: true,
});

const cellPositioner = createMasonryCellPositioner({
    cellMeasurerCache: cache,
    columnCount: 5,
    columnWidth,
    spacer: 10,
});

const GalleryRecycler = ({ images }) => {
    const [imageUrl, setImageUrl] = useState(null);
    const [imageIdx, setImageIdx] = useState(null);
    const [showImageModal, setShowImageModal] = useState(false);

    const onImageClick = (url, index) => {
        setImageUrl(url);
        setImageIdx(index);
        setShowImageModal(true);
    };

    const onImageNavigation = useCallback(
        (key) => {
            let curIndex = imageIdx;
            switch (key) {
                case "ArrowLeft":
                    curIndex--;
                    break;
                case "ArrowRight":
                    curIndex++;
                    break;
                case "DblClick":
                    const image = images[curIndex];
                    window.location.href = `${baseURL}${image.Key}`;
                    break;
                default:
                    break;
            }
            const image = images[curIndex];
            setImageIdx(curIndex);
            setImageUrl(`${baseURL}${image.Key}`);
        },
        [images, imageIdx]
    );

    const RenderCell = useCallback(
        ({ index, key, parent, style }) => {
            const image = images[index];
            const height = columnWidth || defaultHeight;
            const url = `${baseURL}${image.Key}`;

            const onImageLoad = ({ target: img }) => {
                const width = img.offsetWidth;
                const height = img.offsetHeight;
                console.log(width, height);
            };

            const onNavigation = (key) => {};

            return (
                <CellMeasurer
                    cache={cache}
                    index={index}
                    key={key}
                    parent={parent}
                >
                    <div style={style}>
                        <ImageRenderer
                            url={url}
                            index={index}
                            width={columnWidth}
                            height={height}
                            onImageLoad={onImageLoad}
                            onNavigation={onNavigation}
                            onImageClick={onImageClick}
                        />
                    </div>
                </CellMeasurer>
            );
        },
        [images]
    );

    return (
        <>
            <Masonry
                cellCount={images.length}
                cellMeasurerCache={cache}
                cellPositioner={cellPositioner}
                cellRenderer={RenderCell}
                width={1700}
                height={900}
            />
            <ImageModal
                url={imageUrl}
                isOpen={showImageModal}
                styles={{
                    content: {
                        width: "fit-content",
                        margin: "0 auto",
                    },
                    overlay: {
                        overflow: "scroll",
                        height: "100vh",
                    },
                }}
                onRequestClose={() => setShowImageModal(false)}
                onNavigation={onImageNavigation}
            />
        </>
    );
};

export default GalleryRecycler;
