import React, { useCallback, useContext, useEffect, useMemo } from "react";
import { ImageBucketContext } from "../modules/ImageBucket/ImageBucketContext";
import "react-virtualized/styles.css";
import GalleryRecycler from "./GalleryRecycler";
import InfiniteScroll from "react-infinite-scroll-component";
const MGGallery = () => {
    const { fetchBucket, images, nextToken } = useContext(ImageBucketContext);

    useEffect(() => {
        console.log("In UE");
        fetchBucket();
    }, []);

    const fetchData = useCallback(() => {
        if (nextToken) fetchBucket(nextToken);
    }, [nextToken]);

    const hasMore = useMemo(() => {
        return !!nextToken;
    }, [nextToken]);

    return (
        <>
            <div>
                <p>Gallery</p>
                <div className="grid-gallery-box">
                    <InfiniteScroll
                        dataLength={images.length}
                        next={fetchData}
                        hasMore={hasMore}
                        loader={<h4>Loading...</h4>}
                        endMessage={
                            <p style={{ textAlign: "center" }}>
                                <b>Yay! You have seen it all</b>
                            </p>
                        }
                    >
                        <GalleryRecycler images={images} />
                    </InfiniteScroll>
                </div>
            </div>
        </>
    );
};

export default MGGallery;
