import { useCallback, useState } from "react";
import ImageModal from "./ImageModal";

const baseURL = `https://vvpa-model-gallery.s3.ap-south-1.amazonaws.com/`;
const ImageRenderer = ({
    url,
    index,
    height,
    width,
    onImageLoad,
    onNavigation,
    onImageClick,
}) => {
    const [isLandscape, setIsLandscape] = useState(false);
    const [showModal, setShowModal] = useState(false);

    const onClick = useCallback(() => {
        invalidateModal();
        onImageClick(url, index);
    }, [url, index]);

    const invalidateModal = useCallback(() => {
        setShowModal(!showModal);
    }, [showModal]);

    return (
        <>
            <img
                src={url}
                alt="model"
                onLoad={onImageLoad}
                onClick={onClick}
                className={`img-grid22 ${
                    isLandscape ? "landscape" : "portrait"
                }`}
                style={{
                    height: height,
                    width: width,
                    display: "block",
                    objectFit: "cover",
                }}
            />
        </>
    );
};

export default ImageRenderer;
